// 封装的局部混入（vue实例中声明的都是局部混入）的对象 组件参数选项  (data props created mounted computed methods watch ...)
// 提供一个公有的组件参数选项，不能入vux 不能随意通信
// 全局混入有限制
export const myMixin = {
    data(){
        return {
            mobile:localStorage.mobile||"",
            count:1903520,
            city:{}
        }
    },
    created(){
        // console.log("这是一个混入的钩子函数")
        if(localStorage.city){
            var city = JSON.parse(localStorage.city);
            console.log(city);
            this.city = city;
        }else{
            this.$router.push({name:"city"})
        }
    },
    mounted(){
        
    },
    filters:{

    },
    computed:{
        
    }
}


// 混入动画  
export const animate = {
    data(){
        return {
            animateClass:"enter"
        }
    },
    beforeRouteEnter(to,from,next){
        // console.log("beforeRouteEnter")
        // this 取不到  
        // 路由进入之前  组件实例还未获取到  所以这里不要使用 this 
        next();
    },
    beforeRouteLeave(to,from,next){
        // console.log("beforeRouteLeave");
        this.animateClass = "leave";
        setTimeout(()=>{
            next();
        },400)
    }
}