<template>
  <div>
    <input type="file" class="fileone" ref="file" @change="uploadFile" />
    <img :src="pic" alt="err" class="avatar" @click="handleClick" />
  </div>
</template>
//  /* input输入框的onchange事件，要在 input 失去焦点的时候才会触发；
//       在输入框内容变化的时候不会触发change 当鼠标在其他地方点一下才会触发；*/

<script>
import pic from "@/assets/images/photo.png";
import {
    baseURL
} from "@/utils/axios.js";

export default {
    data() {
        return {
            pic
        };
    },
    mounted(){
        // 判断本地存储  有没有头像 
        // 如果有 判断手机号是否一样 
        // 如果手机号一样 直接获取本地的  
        // 如果手机号不一样  就再获取数据库  
        if(localStorage.users){  
            var users = JSON.parse(localStorage.users);
            if(users.mobile==localStorage.mobile){
                this.pic = users.path;   // 没有请求ajax 
            }else{
                this.getAvatar();
            }
        }else{
            this.getAvatar();
        }
    },
    methods: {
        getAvatar(){  // 默认的请求数据库头像 
            this.$axios.post("/vue/getavatar")
            .then(res=>{
                if(res.data.type){
                    this.pic = res.data.result.avatar.replace(/public/,baseURL);
                    let users = {
                        path:this.pic,
                        mobile:res.data.result.mobile
                    }
                    localStorage.setItem("users",JSON.stringify(users));  // 存储头像 
                }else{
                    this.pic = pic;  // 最后使用 本地默认的统一头像 
                }
            })
        },
        handleClick() {
            this.$refs.file.click();
        },
        uploadFile() {
            console.log("开始长传头像...");
            var file = this.$refs.file.files[0]; // input 上传 可以上传多个文件 
            console.log(file)
            var data = new FormData(); //只能通过post请求上传文件 FormData 浏览器的内置对象  实例化一个表单对象  (appliaction/formdata)
            // data  对象   data.username = username
            // data.mobile = mobile ;
            data.append("avatar",file); //给表单绑定所上传的文件    给data对象添加一个属性  配置 key  为  avatar
            // 头像上传   node.js 需下载 cnpm i multer -S 插件
            this.$axios({
                url:"/vue/uploadimg",
                method:"POST",
                data:data
            }).then(res=>{
                this.pic = res.data.path.replace(/public/,baseURL);  // 方法1 
                console.log(this.pic);
                // this.pic = '/' +  res.data.path;
                let users = {
                    path:this.pic,
                    mobile:res.data.mobile
                }
                localStorage.setItem("users",JSON.stringify(users));  // 存储头像 
            })
        }
    }
};
</script>

<style lang="scss">
.fileone {
  display: none;
}
.avatar {
  width: 120px;
  height: 120px;
  border-radius: 50%;
}
</style>