/* eslint-disable  */    //这句话使得注释、空格bu报错
import Vue from 'vue'//导入VUE模块
import App from './App.vue'//App 是vue根组件
import './registerServiceWorker'
import router from './router'//导入路由中的index.js
import store from './store'

Vue.config.productionTip = false

//footer bug
import Router from 'vue-router'
const routerPush = Router.prototype.push
Router.prototype.push = function push(location) {
return routerPush.call(this, location).catch(error=> error)
}


import "lib-flexible";//淘宝适配必须引入的

// 全局样式导入
import "@/styles/index.scss";

import "@/utils/quan.js"//注册全局组件

import {axios} from "@/utils/axios.js"//全局导入axios,只供单文件组件使用
Vue.prototype.$axios = axios;//将axios绑定到vue的原型上，使得this.$axios = axios



// 引入Vant UI（方式三：导入所有组件）
// import Vant from 'vant';
// import 'vant/lib/index.css';
// Vue.use(Vant);   配置按需引入后，将不允许直接导入所有组件

//引入Vant UI（方式一：导入所需组件）
import "@/utils/vant.js"






// 4.挂载路由到根实例对象
new Vue({
    router,//挂载路由 this.$router
    store,//挂载this.$store 来获取 vuex的store 对象
    render: h => h(App)//render Vue 渲染虚拟DOM的函数
}).$mount('#app')//把虚拟DOM APP 载入到div#app里

// h=>h(App)
// (h)=>{return h(App)}


// Vue的核心文件  启动项目