    <h2>count === {{count1}} / {{count2}} / {{newCount}}</h2>
    <h2>word == {{word1}}</h2>
    <p>
      <input type="text" :value="word1" @input="changeWord($event)" />
    </p>
    <h2>msg == {{msg1}}</h2>
    -->
    <h2>city == {{ city1 }} / {{city2}}</h2>
    <h2>count === {{count1}} / {{count2}} / {{0}}</h2>
    <h2>word == {{word1}}</h2>
    <p>
