    <h2>msg == {{msg}}</h2>

    <p v-for="(py,i) in yum.players" :key="i">{{py.name}}</p>

    <van-button type="primary" @click="changeNum">修改foot num</van-button>
    <van-button type="danger" @click="changeCount(10)">修改 count + 10</van-button>
    <van-button type="warning" @click="countDesc">修改 count --</van-button>
    <van-button type="warning" @click="changeCity('beautiful wuhan')">我想去 美丽的 武汉</van-button>
    <van-button type="primary" @click="changeMsgAction">异步数据msg 修改</van-button>
  </div>
</template>

// computed  不能带（），而Vue的{{}}中的methods方法 必须带（）,属性中随意

<script>
import { mapState, mapGetters, mapMutations, mapActions } from "vuex";

import { CHANGEWORD, CHANGECITY, COUNTDESC } from "@/store/types";
export default {
  data() {
    //初始化数据  vuex 取值 永远不要使用data，因为不能响应式数据更新
    return {
      city1: this.$store.state.city, // error
      count1: this.$store.state.count
    };
  },
  methods: {
    ...mapMutations({
      changeNum: "changeNum" // 将 `this.changeNum()` 映射为 `this.$store.commit('changeNum')`
    }),
    ...mapMutations([
      "changeCount", // this.changeCount  映射到this.$store.commit("changeCount")
      COUNTDESC,
      CHANGECITY, // this.changeCity  映射到this.$store.commit("changeCity")  已传参（事件绑定）
      CHANGEWORD // this.changeWord    同理
    ]),
    ...mapActions({
      changeMsgAction: "changeMsg" // this.changeMsgAction == this.$store.dispatch('changeMsg')
    }),
    ...mapActions([
      "getPlayers" // this.getPlayers = this.$store.dispatch('getPlayers')
    ]),

    // changeNum() {
    //   //state中的数据发生改变，必须先提交this.$store.commit( mutations 中的方法)
    //   this.$store.commit("changeNum"); // commit mutation 提交
    // },
    // changeCount(num) {
    //   // 提交格式：commit(type,payload);
    //   this.$store.commit("changeCount", num); // mutation type
    // },
    // // countDesc() {
    // //   this.$store.commit("countDesc");
    // // },
    // // changeCity(city) {
    // //   this.$store.commit("changeCity", city);
    // // },
    // // changeWord(e) {
    // //   //   this.word = "I Miss YOu"  错误操作
    // //   this.$store.commit("changeWord", e.target.value);
    // // },
    // countDesc() {
    //   this.$store.commit(COUNTDESC);
    // },
    // changeCity(city) {
    //   this.$store.commit(CHANGECITY, city);
    // },
    // changeWord(e) {
    //   // this.word = "I Miss YOu"
    //   this.$store.commit(CHANGEWORD, e.target.value);
    // },
    // changeMsgAction() {
    //   // dispatch 发送 action
    //   this.$store.dispatch("changeMsg");
    // }
  },

  computed: {
    //mapState前提：computed  计算属性中
    ...mapState({
      // (...) 使得mapState与局部计算属性混合
      city2: state => state.city,
      count2: state => state.count,
      word1: "word" // 'word' 等同于 `state => state.word`
    }),
    ...mapState([
      //当映射的计算属性的名称与 state 的子节点名称相同时，我们也可以给 mapState 传一个字符串数组
      "count", // 映射(变量名必须一致) this.count 为 store.state.count
      "city",
      "msg",
      "yum"
    ]),
    ...mapGetters(["newCount"])
  },

  // computed: mapState({
  //   city2: state => state.city,
  //   count2: state => state.count,
  //   word1: "word" // 'word' 等同于 `state => state.word`
  // }),

  // computed: {
  //   // 依赖属性
  //   city2() {
  //     return this.$store.state.city;
  //   },
  //   count2() {
  //     return this.$store.state.count;
  //   },
  //   newCount() {
  //     return this.$store.getters.newCount;
  //   },
  //   word1: {
  //     get() {
  //       return this.$store.state.word;
  //     }
  //     //   set(newVal) {
  //     //     console.log(newVal);
  //     //     // return newVal;   error  不能有return
