<template>
    <footer>
        <div v-for="(foot,i) in foots" :key="i">
            <router-link :to="{name:foot.name}">
                <i class="iconfont" v-html="foot.icon"></i>
                <span>{{foot.txt}}</span>
                <van-tag class="hot" v-if="i==3" :color="color">0</van-tag>
            </router-link>
        </div>
    </footer>
</template>

<script>
let foots = [//Unicode编码需要HTML来解析
    {txt:"电影",name:"movie",path:"/main/movie",icon:"&#xe67d;"},
    {txt:"影院",name:"cinema",path:"/main/cinema",icon:"&#xe655;"},
    {txt:"资讯",name:"news",path:"/main/news",icon:"&#xe638;"},
    {txt:"电商",name:"shop",path:"/main/shop",icon:"&#xe61c;"},
    {txt:"我的",name:"mine",path:"/main/mine",icon:"&#xe505;"},
]
export default {
    data(){
        return {
			foots,
			color:this.$route.name == "shop"?"orange":"#999",
        }
    },
    methods:{
        
	},
	watch:{//"$route" 不懂？？？？？
		"$route"(to){
			to.name === "shop"?this.color="orange":this.color="#999";
		}
	}
}
</script>
<style lang="scss" scoped>
footer{
	position:fixed;
	width:100%;
	height:45px;
	left:0;
	bottom:0;
	background:#fff;
	display:flex !important;
    z-index:100;
    border-top:1px solid black;
	div{
		flex:1;
		height:45px;
		a{
			display: block;
			width:100%;
			height:100%;
			display:flex;
			flex-direction:column;
			box-sizing:border-box;
			padding:3px 0;
			text-align:center;
			font-family:"微软雅黑";
			color:#999;
			position: relative;
			i{
			width:100%;
			height:23px;
			line-height:23px;
			font-size:22px !important;
			}
			span{
				width:100%;
				height:19px;
				line-height:19px;
				font-size:14px;
			}
			.hot{
				position: absolute;
				top:0;
				left:60%;
				display: inline-block;
                width:10px;/*no*/
                height:10px;
                font-size:10px;
                line-height: 10px;
                border-radius: 50%;
			}
		}
		.nav-active,.router-link-exact-active,.router-link-active{
			color:orange;
		    border-color:orange;
		}

	}
}
</style>