import Vue from 'vue'
// 0.vue模块化 全局注册，引入VueRouter
import VueRouter from 'vue-router'
// 0.vue模块化 全局注册，引入VueRouter  Vue.use(VueRouter)
Vue.use(VueRouter);

// 1.路由组件选项
import Error from '../views/Error.vue'

// 2.定义路由信息  配置路由组件的映射关系
const routes = [
  {
    path:"/",
    redirect:{name:"guide"},
  },{
    path:"/guide",
    name:"guide",
    component:()=>import("@/views/Guide.vue")
  },
  {
    path:"/main",
    component:()=>import("@/views/Main.vue"),
    redirect:"/main/movie",
    children:[
      {
        path:"movie",
        name:"movie",
        component:()=>import("@/views/Movie.vue"),
      },
      {
        path:"movies",
        name:"movies",
        component:()=>import("@/views/Movies.vue"),
      },
      {//子路由的path不能加 /
        path:"news",
        name:"news",
        component:()=>import("@/views/News.vue"),
      },
       {//子路由的path不能加 /
        path:"cinema",
        name:"cinema",
        component:()=>import("@/views/Cinema.vue"),
      },
      {//子路由的path不能加 /
        path:"mine",
        name:"mine",
        component:()=>import("@/views/Mine.vue"),
      },
      {//子路由的path不能加 /
        path:"shop",
        name:"shop",
        component:()=>import("@/views/Shop.vue"),
      },

    ]
  },
  {
    path:"/seach",
    name:"search",
    component:()=>import("@/views/Search.vue")
  },
  {
    path:"/city",
    name:"city",
    component:()=>import("@/views/City.vue")
  },
  {
    path:"/yydetail/:cinemaId",
    name:"yydetail",
    component:()=>import("@/views/Cinemadetail.vue")
  },
  {
    path:"/login",
    name:"login",
    component:()=>import("@/views/Login.vue")
  },
  {
    path:"/films/:filmId",
    name:"films",
    component:()=>import("@/views/Films.vue")
  },
  {
    path:"/404",
    name:"404",
    component:Error,
  },
  {
    path:"*",
    redirect:"/404",
    
  },

  
]
//3.创建路由实例对象
const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})


// // 路由进入之前
// router.beforeEach((to,from,next)=>{
//     document.getElementsByClassName('box').className = "box enter";
//     next();
// })


// router.push("/").catch(err=>{
//     console.log("路由错误 -- " + err);
// })

export default router

